"use server"
import { prisma } from "@/lib/prisma";
import CheckAutenticated from "./checkAuthenticates";

export default async function UpdateWebContainerState(chatId : string , files : Array<Object>) {
    
    const response = await CheckAutenticated();
    if(response.success == false) return {
                success : false,
                message : "Authorisation Error",
                data : null
    }
    else {
        try {
                const newState = await prisma.chat.update({
                    where : {
                        id : chatId,
                    },
                    data : {
                        state : {
                            update : {
                                files : JSON.stringify(files)
                            }
                        }
                    },
                    select : {
                        state : {
                            select : {
                                files : true
                            }
                        }
                    }
                
                })
    
            return {
                success : true,
                message : "Updated the State",
                data : newState
            }

        }
        catch (err) {
            console.log(err)
            return {
                success : false,
                message : "Database Error",
                data : null
            }
        }
    }
}