"use client"
import { PlusIcon, Router, SendIcon } from "lucide-react";
import { Button } from "../ui/button";
import { Card, CardContent, CardHeader } from "../ui/card";
import { Input } from "../ui/input";
import { useContext, useEffect, useRef } from "react";
import { axiosInstance } from "@/app/axiosInstance";
import { toast, Toaster } from "sonner";
import { userContext } from "@/app/context/user";
import { useRouter } from "next/navigation";
import { XMLParser } from "fast-xml-parser";
import { BoltAction, ChatMessages, Step } from "@/app/types/user";
import { useSession } from "next-auth/react";
import CreateChat from "@/app/helpers/createChat";
import { resolve } from "path";
import { parseStringPromise } from "xml2js";
import { all } from "axios";
import { parseDocument } from "htmlparser2";
import { DomUtils } from "htmlparser2";
import CreateMessage from "@/app/helpers/createMessage";
import { title } from "process";
import UpdateWebContainerState from "@/app/helpers/updateWebContainerState";
export default function ChatboxLanding() {
  const session = useSession();

  const router = useRouter();

  // if(session == null || session.data?.user != null) {
  //     return 
  // }


  const { setUserPrompt, userChats, setUserChats, allMessages, setAllMessages } = useContext(userContext)!;
  const prompt = useRef<HTMLInputElement | null>(null)

  async function xmlToString(xml: string) {
    const cleanedXml = xml.replace(/^```xml\s*/, "").replace(/\s*```$/i, "");
    const doc = parseDocument(cleanedXml);
    const actions = DomUtils.findAll((el) => el.name === "boltaction", doc.children);

    const allSteps: Step[] = actions.map((el) => ({
      title: el.attribs.filepath,
      status: "Pending",
      content: DomUtils.getText(el), // preserves inner HTML/text
    }));;
    return allSteps

  }


  async function handlePrompt(userPrompt: string) {
    try {
      const response = await axiosInstance.post("/template", {
        userPrompt
      })

      console.log(response.data);
      return response.data.message.prompts
    }
    catch (err) {
      console.log(err);
      return { BASE_PROMPT: "", uiPrompt: "", userPrompt: "" }
    }
  }


  type TreeNode = {
    name: string;
    folder?: boolean;
    file?: boolean;
    content?: string;
    children?: TreeNode[];
  };

  type InternalNode = {
    name: string;
    folder?: boolean;
    file?: boolean;
    content?: string;
    childrenMap?: Record<string, InternalNode>;
  };

  function buildTreeUI(files: { title: string; content: string }[]): TreeNode[] {
    const rootMap: Record<string, InternalNode> = {};

    for (const file of files) {
      const parts = file.title.split("/");
      let currentLevel = rootMap;

      for (let i = 0; i < parts.length; i++) {
        const part = parts[i];
        const isFile = i === parts.length - 1;

        if (!currentLevel[part]) {
          currentLevel[part] = isFile
            ? { name: part, file: true, content: file.content }
            : { name: part, folder: true, childrenMap: {} };
        }

        if (!isFile) {
          currentLevel = currentLevel[part].childrenMap!;
        }
      }
    }

    function mapToArray(nodeMap: Record<string, InternalNode>): TreeNode[] {
      return Object.values(nodeMap).map((node) => {
        const { childrenMap, ...rest } = node;
        const treeNode: TreeNode = { ...rest };
        if (childrenMap) {
          treeNode.children = mapToArray(childrenMap);
        }
        return treeNode;
      });
    }

    return mapToArray(rootMap);
  }



  type ResponseForTemplate = { BASE_PROMPT: string, uiPrompt: string, userPrompt: string };

  async function promptResponseFromAi(payload: ResponseForTemplate, prompt: string, chatId : string) {
    try {
      const response = await axiosInstance.post("/take-prompt", { ...payload, userPrompt: prompt });

      //removing the unwanted characters
      const modifiedXML = response.data.message.slice(7, -4);

      //we got the xml file in response , now we convert into steps
      const correctedXML = await xmlToString(modifiedXML);
      console.log(correctedXML)
      //here i am updating the state of the webContainer
      const webContainerFileStructure = buildTreeUI(correctedXML);
      await UpdateWebContainerState(chatId,webContainerFileStructure)

      //here i am creating the message of user and ai(first message)
      const messageResponse = await CreateMessage(chatId, prompt, JSON.stringify(correctedXML))
      if (messageResponse.data != null) {

        let messages: ChatMessages[] = [];
        for (let i = 0; i < messageResponse.data.messages.length; i++) {
          const obj = {
            user: messageResponse.data.messages[i].userMessage,
            ai: JSON.parse(messageResponse.data.messages[i].ai)
          }

          messages.push(obj);
        }

        //now i am setting the message(updating the state)
        setAllMessages(messages);
      }

    } catch (err) {
      console.log(err)
      toast.error("Ai Model is not Running");
    }
  }




  return (
    <div className="w-130 h-45">
      <Toaster />
      <Card>
        <CardHeader className="text-[15px] font-semibold">Create Any Website by Just Prompting..</CardHeader>
        <CardContent>
          <div className="flex gap-3">
            <Input ref={prompt} className="h-19" placeholder="What you are creating today Just paste your thoughts." />

            <div className="flex flex-col gap-1 justify-end">
              <Button ><PlusIcon /></Button>

              <Button onClick={async () => {
                if (prompt.current != null && prompt.current.value != "") {
                  //here we do the database call 
                  // setUserChats([...userChats, {title ,id}])

                  setUserPrompt(prompt.current.value);
                  console.log(prompt.current.value)

                  const chats = await CreateChat(prompt.current.value);
                  console.log(chats)
                  if (chats.success == false) {
                    if (chats.message == "Authorisation Error") router.push("/");
                    else toast.error("Chat not Created");
                  }
                  else if (chats.success == true && chats.data != null) {
                    setUserChats(chats.data.chats);
                    const response = (await handlePrompt(prompt.current.value));
                    console.log("response", { ...response, userPrompt: prompt.current.value })
                    const chatId = chats.data.id;
                    const response2 = await promptResponseFromAi(response, prompt.current.value, chatId);
                    router.push(`/chat/${chatId}`);
                  }

                }
                else {
                  toast.error("Prompt is Empty. Write Correct Prompt")
                }
              }}><SendIcon /></Button>
            </div>

          </div>
        </CardContent>
      </Card>
    </div>
  )
}